.checkbox-container {
    display: flex;
    align-items: center;
  }
  
  .checkbox-container.top {
    flex-direction: column;
    align-items: center;
  }
  
  .checkbox-container.bottom {
    flex-direction: column;
    align-items: center;
  }
  
  .checkbox-container.start {
    flex-direction: row-reverse;
  }
  
  .checkbox-container.left {
    flex-direction: row;
  }
  